import { Component, EventEmitter, OnDestroy, OnInit, Output } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ComponentProps, ComponentRef } from '@ionic/core';
import { NavigationExtras, Router } from '@angular/router';
import { SelectItem } from 'primeng/api';
import { Subscription, firstValueFrom } from 'rxjs';
import { Dropdown } from 'primeng/dropdown';

import { ToastMessageService } from './../../../../services/toast/toast-message.service';
import { PageableDto } from './../../../../interfaces/others/pageable.dto';
import { ToastEnum, ToastPrimeSeverityEnum } from './../../../../constants/toast.constant';
import { PageOrder } from './../../../../constants/page.constant';

import { ConfiguracaoAprovacaoService } from './../../../../services/fluxo-pagamentos/configuracoes-aprovacao/configuracao-aprovacao.service';
import { TipoOperacaoSaidaDto } from './../../../../interfaces/fluxo-pagamentos/tipos-operacao-saida/tipo-operacao-saida.dto';
import { TipoOperacaoSaidaService } from './../../../../services/fluxo-pagamentos/tipos-operacao-saida/tipo-operacao-saida.service';
import { OperacoesCreateComponent } from '../operacoes-create/operacoes-create.component';
import { OperacoesDetailComponent } from '../operacoes-detail/operacoes-detail.component';

@Component({
  selector: 'app-operacoes-main',
  templateUrl: './operacoes-main.component.html',
  styleUrls: ['./operacoes-main.component.scss'],
})
export class OperacoesMainComponent implements OnInit, OnDestroy {

  operacoes: PageableDto<TipoOperacaoSaidaDto>;
  searchedOperacao = '';
  selectedOperacao: TipoOperacaoSaidaDto;
  currentPage = 1;

  private subscriptions = new Subscription();

  constructor(
    private readonly modal: ModalController,
    private readonly toastMessageService: ToastMessageService,
    private readonly tipoOperacaoSaidaService: TipoOperacaoSaidaService,
    private readonly configuracaoAprovacaoService: ConfiguracaoAprovacaoService,
  ) { }

  ngOnInit() {
    this.loadOperacao();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadOperacao(page = 1, dropdown?: Dropdown) {
    const sub = this.tipoOperacaoSaidaService.list(
      { searchedTipoOperacao: this.searchedOperacao, },
      { order: PageOrder.ASC, page, take: 10 })
      .subscribe(data => {
        this.operacoes = data;
        if (dropdown) {
          dropdown.show();
        }
      });

    this.subscriptions.add(sub);
  }

  async createNew({ id, nome }: TipoOperacaoSaidaDto) {
    this.toastMessageService.clearToast();
    let data;

    try {
      data = await firstValueFrom(this.configuracaoAprovacaoService.findById(id));
    } catch (e) {
      this.toastMessageService.clearToast();
    }


    this.showModal(OperacoesCreateComponent, { configuracaoAtual: data, tipoOperacaoNome: nome, tipoOperacaoId: id }, ['modal-size-80']);
  }

  detalhar({ id }: TipoOperacaoSaidaDto) {
    this.toastMessageService.clearToast();
    this.configuracaoAprovacaoService.findById(id)
      .subscribe({
        next: (data) => {
          this.showModal(OperacoesDetailComponent, { configuracaoAtual: data }, ['modal-size-80', 'modal-transparent']);
        }
      });
  }

  search() {
    this.loadOperacao(1);
  }

  paginar(event) {
    const { first, rows } = event;
    const page = Number((Number(first) / Number(rows)) + 1);
    if (page != this.currentPage) {
      this.currentPage = page;
      this.loadOperacao(page);
    }
  }

  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps,
    cssClass = ['modal-size-80'],
  ) {
    const modal = await this.modal.create({
      component,
      backdropDismiss: false,
      cssClass,
      componentProps,
    });

    modal.onDidDismiss()
      .then((data) => {
        const { data: hasUpdate, role } = data;

        if (hasUpdate) {
          this.loadOperacao(this.currentPage);
          if (role == 'create') {
            this.toastMessageService.presentToast({
              detalhe: `Pagamento criado com sucesso!`,
              titulo: `Sucesso!`,
              duracao: ToastEnum.mediumDuration,
              gravidade: ToastPrimeSeverityEnum.SUCESSO,
            });
          }
        }

      });

    return await modal.present();
  }


}
