import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';

import { Subscription } from 'rxjs';
import { ConfiguracoesAprovacaoDto } from './../../../../interfaces/fluxo-pagamentos/configuracoes-aprovacao/configuracao-aprovacao.dto';
import { UserDto } from './../../../../../shared/interfaces/users/user.dto';

@Component({
  selector: 'app-operacoes-detail',
  templateUrl: './operacoes-detail.component.html',
  styleUrls: ['./operacoes-detail.component.scss'],
})
export class OperacoesDetailComponent implements OnInit, OnDestroy {
  //TODO -> Solicitacoes
  @Input()
  configuracaoAtual: ConfiguracoesAprovacaoDto;

  hasUpdate = false;

  private subscriptions = new Subscription();
  constructor(
    private readonly modalCtrl: ModalController,
  ) { }

  ngOnInit() {
    console.log(this.configuracaoAtual);
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }


  setUserImage(user: UserDto) {
    return user.avatar_url || "assets/imgs/outros/default-user.jpg";
  }


  fechar() {
    this.modalCtrl.dismiss(this.hasUpdate);
  }

}
