import { ComponentProps, ComponentRef } from '@ionic/core';
import { ModalController } from '@ionic/angular';
import { Component, Input, OnDestroy, OnInit } from '@angular/core';

import { ConfirmationService } from 'primeng/api';
import { Subscription } from 'rxjs';
import { Dropdown } from 'primeng/dropdown';
import { v4 as uuidV4 } from "uuid";

import { ConfiguracoesAprovacaoDto } from './../../../../interfaces/fluxo-pagamentos/configuracoes-aprovacao/configuracao-aprovacao.dto';
import { PerfilDto } from './../../../../interfaces/perfis/perfil.dto';
import { PageableDto } from './../../../../interfaces/others/pageable.dto';
import { PageOrder } from './../../../../constants/page.constant';
import { CreateNiveisAprovacaoDto, NivelAprovacaoDto } from './../../../../interfaces/fluxo-pagamentos/niveis-aprovacao/nivel-aprovacao.dto';
import { ConfiguracaoAprovacaoService } from './../../../../services/fluxo-pagamentos/configuracoes-aprovacao/configuracao-aprovacao.service';
import { PerfilService } from './../../../../services/perfil/perfil.service';
import { ToastMessageService } from './../../../../services/toast/toast-message.service';
import { ToastEnum } from './../../../../constants/toast.constant';
import { ToastPrimeSeverityEnum } from './../../../../../shared/constants/toast.constant';


@Component({
  selector: 'app-operacoes-create',
  templateUrl: './operacoes-create.component.html',
  styleUrls: ['./operacoes-create.component.scss'],
})
export class OperacoesCreateComponent implements OnInit, OnDestroy {

  @Input()
  configuracaoAtual: ConfiguracoesAprovacaoDto;

  @Input()
  tipoPerfilNome: string;

  @Input()
  tipoOperacaoId: string;

  perfis: PageableDto<PerfilDto>;
  searchedPerfil = '';
  selectedPerfil: PerfilDto;


  showDialog = false;

  niveis_aprovacao: NivelAprovacaoDto[] = [];
  nivelIndexToUpdate: number;

  private hasUpdate = false;
  private subscriptions = new Subscription();

  constructor(
    private readonly modalCtrl: ModalController,
    private readonly perfilService: PerfilService,
    private readonly configuracaoAprovacaoService: ConfiguracaoAprovacaoService,
    private readonly confirmationService: ConfirmationService,
    private readonly toastService: ToastMessageService,
  ) { }

  ngOnInit() {
    this.niveis_aprovacao = this.configuracaoAtual?.niveis_aprovacao || [];
    this.loadPerfil();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }


  //#region PERFIS
  loadPerfil(page = 1, dropdown?: Dropdown) {
    const sub = this.perfilService.list(
      { order: PageOrder.ASC, page, take: 10 })
      .subscribe(data => {
        this.perfis = data;
        if (dropdown) {
          dropdown.show();
        }
      });

    this.subscriptions.add(sub);
  }
  paginarPerfil(event) {
    this.loadPerfil();
  }
  onPerfilFilter(event) {
    const filter = event.filter as string;
    this.searchedPerfil = filter;
  }
  blurPerfil(event, dropdown: Dropdown) {
    this.searchedPerfil = undefined;
    dropdown.filterValue = undefined;
  }

  //#endregion

  getVersaoAtual() {
    const versao = (Number(this.configuracaoAtual?.versao) + 1);
    return Number.isNaN(versao) ? '1' : versao;
  }

  salvarNivel() {
    if (this.nivelIndexToUpdate >= 0) {
      this.niveis_aprovacao[this.nivelIndexToUpdate].perfil_aprovador = this.selectedPerfil;
    } else {
      this.niveis_aprovacao.push({
        id: uuidV4(),
        ordem: undefined,
        user_registrou: undefined,
        perfil_aprovador: this.selectedPerfil,
      });
    }

    this.closeDialog(null);
  }

  editNivel(nivel: NivelAprovacaoDto) {
    const index = this.niveis_aprovacao.findIndex(n => n.id == nivel.id);
    const selectedNivel = this.niveis_aprovacao[index];
    this.selectedPerfil = selectedNivel.perfil_aprovador;


    this.nivelIndexToUpdate = index;
    this.showDialog = true;
  }

  deleteNivel(nivel: NivelAprovacaoDto, ordem: number) {
    this.confirmationService.confirm({
      message: `Deseja mesmo deletar o nível ${ordem + 1}?`,
      header: `Deleção`,
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        const index = this.niveis_aprovacao.findIndex(n => n.id == nivel.id);
        this.niveis_aprovacao.splice(index, 1);

        this.toastService.presentToast({
          titulo: `Sucesso`,
          detalhe: `Nível deletado com sucesso!`,
          duracao: ToastEnum.shortDuration,
          gravidade: ToastPrimeSeverityEnum.SUCESSO,
        });
      },
      reject: (type) => {
        this.toastService.clearToast();
      }
    });


  }

  disableSalvarNivelBtn() {
    return  !this.selectedPerfil;
  }

  closeDialog(event) {
    this.selectedPerfil = undefined;
    this.nivelIndexToUpdate = undefined;
    this.showDialog = false;
  }

  fechar() {
    this.confirmationService.close();
    this.modalCtrl.dismiss(this.hasUpdate);
  }

  atualizar() {
    this.confirmationService.confirm({
      message: `As solicitações em andamento seguirão o fluxo atual, mas as novas seguirão o novo fluxo.`,
      header: `Deseja prosseguir?`,
      icon: 'pi pi-exclamation-triangle',
      accept: () => {

        const niveis_aprovacao: CreateNiveisAprovacaoDto[] = this.niveis_aprovacao.map(n => {
          return {
            
            perfil_aprovador_id: n.perfil_aprovador?.id || undefined,
          }
        });

        const add = this.configuracaoAprovacaoService.create({
          tipo_operacao_id: this.tipoOperacaoId,
          niveis_aprovacao,
        })
          .subscribe({
            next: (response) => {
              this.toastService.presentToast({
                titulo: `Sucesso`,
                detalhe: `Configuração atualizada com sucesso!`,
                duracao: ToastEnum.shortDuration,
                gravidade: ToastPrimeSeverityEnum.SUCESSO,
              });
              this.hasUpdate = true;
              this.fechar();
            }
          });

        this.subscriptions.add(add);
      },
      reject: (type) => {
        this.toastService.clearToast();
      }
    });


  }

}
